  _renderSVG: function() {
    var svg = dom(this.$.svg);
    var path = document.createElementNS("http://www.w3.org/2000/svg", "path");

    dom(path).setAttribute("fill", this.color);
    dom(path).setAttribute("d", this.path);

    svg.appendChild(path);
  },

  // Fires when the `<social-media-icons>` has been fully prepared
  ready: function() {
    // If `color` is not defined in Light DOM, fill the icon with brand color
    if (!this.color) {
      switch (this.icon) {
        case "dribbble":
          this.color = "#EA4C89";
          break;
        case "facebook":
          this.color = "#3B5998";
          break;
        case "github":
          this.color = "#171515";
          break;
        case "googleplus":
          this.color = "#DB4E3F";
          break;
        case "instagram":
          this.color = "#3F729B";
          break;
        case "jsfiddle":
          this.color = "#4679BD";
          break;
        case "lastfm":
          this.color = "#D51007";
          break;
        case "linkedin":
          this.color = "#0077B5";
          break;
        case "medium":
          this.color = "#231F20";
          break;
        case "quora":
          this.color = "#A72723";
          break;
        case "pinterest":
          this.color = "#CB2027";
          break;
        case "skype":
          this.color = "#00AFF0";
          break;
        case "spotify":
          this.color = "#6AE368";
          break;
        case "stumbleupon":
          this.color = "#EF4E23";
          break;
        case "tumblr":
          this.color = "#35465C";
          break;
        case "twitter":
          this.color = "#55ACEE";
          break;
        case "youtube":
          this.color = "#E52D27";
          break;
        case "vimeo":
          this.color = "#1AB7EA";
          break;
        case "vine":
          this.color = "#00B489";
          break;
        default:
          this.color = "#000000";
          break;
      }
    }

    this._renderSVG();
  }
